const GetCheckIcons = ({ type }) => {
  switch (type) {
    case 1:
      return <div className="check type"></div>;
    case 2:
      return <div className="late type"></div>;
    case 3:
      return <div className="not type"></div>;
    case 4:
      return <div className="vacancy type"></div>;
    case 5:
      return <div className="illness type"></div>;
    case 6:
      return <div className="vacation type"></div>;
    default:
      return <></>;
  }
};

export default GetCheckIcons;
