import { GetCheckIcons } from 'Components';
import Box from '@mui/material/Box';
import LinearProgress from '@mui/material/LinearProgress';

const checkCloumns = [
  {
    field: 'team',
    headerName: '',
    type: 'string',
    width: 120,
    renderCell: params => {
      return <div className={`${params.value} info`}>{params.value}</div>;
    },
  },
  {
    field: 'role',
    headerName: '',
    type: 'string',
    width: 120,
    renderCell: params => {
      return <div className={`${params.value} info`}>{params.value}</div>;
    },
  },
  {
    field: 'name',
    headerName: '',
    type: 'string',
    width: 120,
  },
  {
    field: 'participateScore',
    headerName: ' ',
    type: 'number',
    width: 120,
  },
  {
    field: 'attendScore',
    headerName: ' ',
    type: 'number',
    width: 120,
  },
  {
    field: 'vacation',
    headerName: '',
    type: 'number',
    width: 120,
  },
  {
    field: 'checkIn',
    headerName: '',
    type: 'string',
    width: 120,
    renderCell: params => {
      return <GetCheckIcons type={params.value} />;
    },
  },
  {
    field: 'checkOut',
    headerName: '',
    width: 120,
    renderCell: params => {
      return <GetCheckIcons type={params.value} />;
    },
  },
  {
    field: 'todoRate',
    headerName: ' ',
    width: 200,
    renderCell: params => {
      return (
        <Box sx={{ width: '100%' }}>
          <LinearProgress variant="determinate" value={params.value} />
        </Box>
      );
    },
  },
];

export default checkCloumns;
