import { useState, useEffect, useContext } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';

import { AuthContext } from 'App';
import { CRUDUserService } from 'Network';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';

const SideBar = () => {
  const [curPage, setCurPage] = useState(null);
  const navi = useNavigate();
  const loca = useLocation();
  const auth = useContext(AuthContext);

  const handleChangePage = (event, value) => {
    console.log('login : ', value);
    setCurPage(value);
    navi(`/${value}`);
  };
  const handleClickLogout = async () => {
    const result = CRUDUserService.postLogout();
    localStorage.clear();
    auth.setStatus(null);
  };

  useEffect(() => {
    let path = loca.pathname.split('/')[1];
    if (!path) path = '';
    setCurPage(path);
  }, []);

  return (
    <>
      <div
        className="home"
        onClick={() => {
          navi('/');
        }}
      >
        <img src="asset/saveme.png" alt="saveme" />
         
      </div>
      {curPage !== null && (
        <Tabs
          orientation="vertical"
          value={curPage}
          onChange={handleChangePage}
        >
          <Tab className="button" label="" value="" />
          <Tab className="button" label="  " value="todo" />
          <Tab className="button" label=" " value="mine" />
          <Tab className="button" label="" value="admin" />
        </Tabs>
      )}
      <div className="button logout" onClick={handleClickLogout}>
        
      </div>
    </>
  );
};

export default SideBar;
