import Button from '@mui/material/Button';
import Chip from '@mui/material/Chip';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';

const SelectedUser = ({
  userInfo,
  onClickChangeAttend,
  onClickChangeTeam,
  onClickChangeRole,
  onClickChangeVacation,
  onClickDeleteUser,
}) => {
  console.log(userInfo);

  return (
    <div className="box">
      <span className="title">  : {userInfo.userName}</span>

      <span className="user-status">
        <Chip
          label={userInfo.attendeStatus}
          className={userInfo.attendeStatus}
        />
        {userInfo.attendeStatus === '' && (
          <>
            <Chip label={userInfo.team} className={userInfo.team} />
            <Chip label={userInfo.role} className={userInfo.role} />
          </>
        )}
      </span>

      <div className="action">
        <Select
          value={userInfo.attendeStatus}
          onChange={onClickChangeAttend}
          displayEmpty
          inputProps={{ 'aria-label': 'Without label' }}
        >
          <MenuItem value={''}></MenuItem>
          <MenuItem value={''}></MenuItem>
        </Select>

        {userInfo.attendeStatus === '' && (
          <>
            <Select
              value={userInfo.team}
              onChange={onClickChangeTeam}
              displayEmpty
              inputProps={{ 'aria-label': 'Without label' }}
            >
              <MenuItem value={'red'}>RED</MenuItem>
              <MenuItem value={'blue'}>BLUE</MenuItem>
            </Select>
            <Select
              value={userInfo.role}
              onChange={onClickChangeRole}
              displayEmpty
              inputProps={{ 'aria-label': 'Without label' }}
            >
              <MenuItem value={''}></MenuItem>
              <MenuItem value={''}></MenuItem>
            </Select>
            <Button
              variant="contained"
              onClick={() => onClickChangeVacation(1)}
            >
               + 0.5
            </Button>
            <Button
              variant="contained"
              onClick={() => onClickChangeVacation(-1)}
            >
               - 0.5
            </Button>
            {/* <Button
            onClick={() =>
              onClickDeleteUser({
                userName: userInfo.userName,
                userId: userInfo.id,
              })
            }
          >
             
          </Button> */}
          </>
        )}
      </div>
    </div>
  );
};

export default SelectedUser;
