const AojiButton = ({ onClickAoji, state }) => {
  return (
    <>
      {!state ? (
        <button onClick={onClickAoji} className="start">
          !
        </button>
      ) : (
        <button onClick={onClickAoji} className="end">
          !
        </button>
      )}
    </>
  );
};

export default AojiButton;
