const userGuide = ({ status }) => {
  const { userName, role, team } = status;
  return (
    <>
      <div>
         {userName}!   {team}
        !
      </div>
      {role === '' ? (
        <> {team}   !</>
      ) : (
        <>     !</>
      )}
    </>
  );
};

export default userGuide;
