import Button from '@mui/material/Button';

const FindTarget = ({ setIsOpen, attendUser }) => {
  const sortArray = attendUser.sort((a, b) => {
    if (a.participateScore === b.participateScore) {
      if (a.attendScore < b.attendScore) return -1;
      else if (a.attendScore > b.attendScore) return 1;
      //    
      else return 0;
    }
    if (a.participateScore > b.participateScore) return -1;
    if (a.participateScore < b.participateScore) return 1;
    return 0;
  });

  return (
    <div className="modal">
      <h1>  </h1>
      <h3>
         5,   3    (3, 2,
        1).
      </h3>
      <h3>   ,   .</h3>
      {/* {sortArray.map((e, i) => {
        return (
          <h1 key={i}>
            {i + 1} - {e.userName} {e.participateScore}
          </h1>
        );
      })} */}
      <h1>
        {sortArray[0].userName} - 
        {sortArray[0].participateScore} -  {sortArray[0].attendScore}
        
      </h1>
      <h1>
        {sortArray[1].userName} - 
        {sortArray[1].participateScore} -  {sortArray[1].attendScore}
        
      </h1>
      <h1>
        {sortArray[2].userName} - 
        {sortArray[2].participateScore} -  {sortArray[2].attendScore}
        
      </h1>
      <Button onClick={() => setIsOpen(false)}></Button>
      <Button onClick={() => setIsOpen(false)}></Button>
    </div>
  );
};

export default FindTarget;
